	module.exports = {"data":{"markdownRemark":{"html":"<p>21世紀の上海発展のために設立された研究テーマであり、それは選択戦略でもあります。国際シンポジウム「21世紀の国際都市をめざして」に参加するHuating Hotelに行きましょう。国と都市の発展には明確な戦略が必要であり、戦略の選択は賢明な開発経路を選択することです。</p>\n<p>北京のT教授は演説で「人間開発を中心とした新しい戦略」を強調した。これは文化的にも高く評価されている。</p>\n<p>「経済は中心である」このコンセプトは、経済が孤立して閉鎖されていないことを示しており、開かれ、常に改革されており、人間社会や自然のあらゆる領域に浸透するだけでなく、あらゆる分野の経済にも浸透している。</p>\n<p>「人間開発を中心とする社会経済戦略は、人間の脳とコンピュータのように簡潔にまとめられています。人間の脳は常に最初のものです」</p>\n<p>これは哲学的意見であり、人間社会の発展動向と一致する意見です。</p>\n<p>過去数年間に、アンマンで開催された国際会議は、過去40年間に、多くの国が人間のニーズ、人間の関心および人間開発にほとんど注意を払っていないとの報告書を国連に提出した。同報告書は、1990年代の国際開発戦略は人の成長に焦点を当て、人々の人生を第一の開発戦略と見なし、人々を将来の経済発展の主な推進力と主要な受益者にすることを勧告している。</p>\n<p>開発の初期段階では、開発途上国は一般的に人間開発にあまり注意を払っていなかったし、注意を払わなかったり、注意を払うリソースがなかったりしたためでもない。経済発展が一定の段階に達した後、この開発戦略を確立する必要があります。さもなければ、経済は最終的に開発が困難になるか、開発され、人々は失われます。比較的低いレベルの経済発展、比較的緊密な社会的資源、そして比較的人口の多い社会において、そのような意識を明確にすることは特に重要である。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0182.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0182.md","frontmatter":{"title":"11月11日 金曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0182.md absPath of file >>> MarkdownRemark"}}

